/**
 *  Vue
 */
export default {
  /**
   * @property {number[]} position 
   * @property {number} [zIndex] 
   * @property {string} [fill] 
   * @property {string} [cursor] 
   */
  props: {
    // 
    position: Array,
    // 
    zIndex: Number,
    // 
    fill: String,
    
    // 
    cursor: String
  },
  watch: {
    position: {
      deep: true,
      handler() {
        //  utils/feature.js 
        this.update()
      }
    }
  },
  computed: {
    coordinate() {
      // 
      return this.position.map(n => Number.parseFloat(n))
    }
  },
  methods: {
    baseStyleRender() {
      return {
        className: 'Style',
        zIndex: this.zIndex,
        fill: this.fill
      }
    }
  }
}
